:root {
  --brand: #227b99;
  --brand-1: #2da7cf;
  --dark-shade: #232435;
  --dark-shade-1: #2d344d;
  --dark-shade-2: #205570;
  --light-accent-1: #c48323;
  --light-accent: #fdb241;
  --light-shade: #a0a0a0;
  --light-shade-1: #dfdfdf;
}
* {
  transition: color 250ms;
  transition-delay: 0ms;
  box-sizing: border-box;
  margin: 0;
  text-align: center;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
    Ubuntu, Cantarell, Helvetica Neue, sans-serif;
}
p,
span.not-p {
  display: block;
  text-align: left;
  font-family: 'Times', sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: x-large;
  color: var(--light-shade-1);
}
h2 {
  color: var(--light-accent);
}
button {
  border-color: var(--dark-shade-1);
  border-width: 1px;
}

button:hover {
  transition: border-color 320ms;
  border-width: 1px;
  border-color: var(--light-accent);
}
p a,
a,
h4 a,
.not-a,
.nav-link {
  text-decoration: none;
  display: inline;
  color: var(--light-shade) !important;
}

p a:hover,
a:hover,
h4 a:hover,
.not-a:hover,
.nav-link:hover,
.nav-link:focus,
.nav-link:active {
  text-decoration: none;
  color: var(--light-accent-1) !important;
}
a div {
  font-weight: 500;
}

