}

.shr-btn,
.shr-btn-l {
  transition: scale 250ms;
}

.shr-btn:focus,
.shr-btn-l:focus {
  outline: none;
}
.no-focus-outline:focus {
  outline: none;
}

.shr-btn:hover {
  animation: enlarge 150ms ease-in forwards;
}
.shr-btn-l:hover {
  animation: enlarge-sm 150ms ease-in forwards;
}

.shr-btn:active {
  animation: shrink 150ms ease-in forwards;
}
.shr-btn-l:active {
  animation: shrink-sm 150ms ease-in forwards;
}
#dropdown-button-drop-up div div {
  background-color: var(--dark-shade-1);
}
.transparent-bg {
  background-color: transparent !important;
}

.shrinkFade {
  animation: shrinkFade 1000ms ease-in forwards;
}
.pos-rel {
  position: relative;
}
.pos-abs {
  position: absolute;
}
.left-0 {
  left: 0;
}

.top-0 {
  top: 0;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#parallax-div-connector {
  background: url('./resources/background-patterns/bg-pblue-str.png');
  background-size: 64px;
  position: absolute;
  height: 34vh;
  width: 25px;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0%);
}
#wrap {
  min-height: 100vh;
}

#tree-cover {
  border-radius: 20px;
  border-width: 100px;
  border-color: var(--dark-shade-2);

  z-index: 100;
  position: absolute;
  width: 300px;
  height: 300px;
  margin: -150px;
  left: 50vw;
  top: 50vh;
  background-color: var(--brand);
}
@keyframes shrinkFade {
  0% {
    visibility: visible;
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(0);
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes enlarge {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

@keyframes shrink {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.8);
  }
}
@keyframes enlarge-sm {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}

@keyframes shrink-sm {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.9);
  }
}
